function ScreenLoader() {
    useEffect(() => {
        // Create the Lottie animation instance
        const animationContainer:any = document.getElementById('animationContainer');
        const animation = lottie.loadAnimation({
          container: animationContainer,
          renderer: 'svg',
          loop: true,
          autoplay: true,
          animationData: animationData,
        });
    
        // Optional: Adjust animation speed
        animation.setSpeed(1.5);
    
        // Optional: Listen to animation events
        animation.addEventListener('complete', () => {
        });
    
        // Cleanup: Destroy the animation on component unmount
        return () => animation.destroy();
      }, []);
    
      return <div id="animationContainer" style={{ width: '112px', height: '112px' }} />;
    };
    
export default ScreenLoader